import "./App.css";
import React, { useState, useEffect } from "react";
import Displayproduct from "./component/Displayproduct";
import DataTable from "./component/Table";

function App() {
  const [effect, setEffect] = useState(false);
  const [product, setProduct] = useState({
    productId: "",
    Name: "",
    price: 0,
    Rating: 0,
    Featured: true,
    company: "",
  });
  const [products, setProducts] = useState([]);
  const addproduct = (product) => {
    fetch("https://productapi-production-02a4.up.railway.app/v1/product", {
      method: "POST",
      headers: new Headers({
        Accept: "application/json",
        "Content-Type": "application/json",
      }),
      body: JSON.stringify(product),
    })
      .then((response) => response.json())
      .then((res) => {
        console.log(res.data);
        if (res.status !== "ERROR") {
          setProducts((prevData) => {
            return [...prevData, product];
          });
          setEffect(true);
        } else {
          alert(res.message);
        }

        return true;
      })
      .catch((error) => {
        alert(error);
      });
  };

  const deleteproduct = (productId) => {
    const url = "https://productapi-production-02a4.up.railway.app/v1/product";
    const fetchData = async () => {
      try {
        const response1 = await fetch(url, {
          method: "DELETE",
          headers: new Headers({
            Accept: "application/json",
            "Content-Type": "application/json",
          }),
          body: JSON.stringify({ productId: productId }),
        })
          .then((response) => response.json())
          .then((res) => {
            setEffect(true);
          })
          .catch((error) => {
            alert(error);
          });
      } catch (error) {
        console.log("error", error);
      }
    };

    fetchData();
  };

  useEffect(() => {
    const url = "https://productapi-production-02a4.up.railway.app/v1/product";

    const fetchData = async () => {
      try {
        const response = await fetch(url, {
          method: "GET",
          headers: new Headers({
            Accept: "application/json",
            "Content-Type": "application/json",
          }),
        });
        const json = await response.json();
        setProducts((prevData) => {
          return json.data;
        });
      } catch (error) {
        console.log("error", error);
      }
    };

    fetchData();
  }, [effect]);
  const featuredproduct = (product) => {
    const url =
      "https://productapi-production-02a4.up.railway.app/v1/product?Featured=true";
    const fetchData = async () => {
      try {
        const response = await fetch(url, {
          method: "GET",
          headers: new Headers({
            Accept: "application/json",
            "Content-Type": "application/json",
          }),
        });
        const json = await response.json();
        console.log(json.data);
        setProducts((prev) => {
          return json.data;
        });
      } catch (error) {
        console.log("error", error);
      }
    };

    fetchData();
  };
  const lowpriceproduct = (e) => {
    e.preventDefault();
    const price = product.price;
    const url = `https://productapi-production-02a4.up.railway.app/v1/product?price=${price}`;
    const fetchData = async () => {
      try {
        const response = await fetch(url, {
          method: "GET",
          headers: new Headers({
            Accept: "application/json",
            "Content-Type": "application/json",
          }),
        });
        const json = await response.json();
        console.log(json.data);
        setProducts((prev) => {
          return json.data;
        });
      } catch (error) {
        console.log("error", error);
      }
    };

    fetchData();
  };

  const ratingproduct = (e) => {
    e.preventDefault();
    const rating = product.Rating;
    const url = `https://productapi-production-02a4.up.railway.app/v1/product?Rating=${rating}`;
    const fetchData = async () => {
      try {
        const response = await fetch(url, {
          method: "GET",
          headers: new Headers({
            Accept: "application/json",
            "Content-Type": "application/json",
          }),
        });
        const json = await response.json();
        console.log(json.data);
        setProducts((prev) => {
          return json.data;
        });
      } catch (error) {
        console.log("error", error);
      }
    };

    fetchData();
  };
  const inputEvent = (event) => {
    const { name, value } = event.target;
    setProduct((prev) => ({
      [name]: value,
    }));
  };
  return (
    <>
      <Displayproduct
        add={addproduct}
        deleteProduct={deleteproduct}
        featured={featuredproduct}
        price={lowpriceproduct}
        rating={ratingproduct}
      />
      <span>
        <button onClick={featuredproduct}>Featured</button>

        <label htmlFor="price">Price less then*:</label>
        <input
          type="number"
          name="price"
          onChange={inputEvent}
          value={product.price}
        />
        <button type="submit" onClick={lowpriceproduct}>
          get Product
        </button>

        <label htmlFor="Rating">Rating greater then*:</label>
        <input
          type="number"
          name="Rating"
          onChange={inputEvent}
          value={product.Rating}
        />
        <button type="submit" onClick={ratingproduct}>
          get Product
        </button>
      </span>
      <DataTable list={products} deleteProduct={deleteproduct} />
    </>
  );
}

export default App;
