import React, { useState } from "react";
import { DataGrid } from "@mui/x-data-grid";
import { Button } from "@mui/material";
import Dialog from "@mui/material/Dialog";
import DialogActions from "@mui/material/DialogActions";
import DialogContent from "@mui/material/DialogContent";
import DialogContentText from "@mui/material/DialogContentText";
import DialogTitle from "@mui/material/DialogTitle";
import Slide from "@mui/material/Slide";
import DeleteButton from "./Delete";
import UpdateButton from "./Update";

const columns = [
  { field: "productId", headerName: "ID", width: 70 },
  { field: "Name", headerName: "Name", width: 130 },
  {
    field: "price",
    headerName: "price",
    width: 130,
    headerAlign: "center",
    align: "center",
  },
  {
    field: "Rating",
    headerName: "Rating",
    width: 130,
    headerAlign: "center",
    align: "center",
  },
  {
    field: "Featured",
    headerName: "Featured",
    width: 130,
    headerAlign: "center",
    align: "center",
  },
  {
    field: "company",
    headerName: "company",
    description: "This column has a value getter and is not sortable.",
    sortable: false,
    width: 130,
    headerAlign: "center",
    align: "center",
  },
];
function createData(Name, productId, price, Rating, Featured, company) {
  return {
    productId,
    Name,
    price,
    Rating,
    Featured,
    company,
  };
}
export default function DataTable(props) {
  const list = props.list.map((p) => {
    return createData(
      p.Name,
      p.productId,
      p.price,
      p.Rating,
      p.Featured,
      p.company
    );
  });
  const [rowselected, setrowselected] = React.useState([]);
  const deleteProduct = () => {
    rowselected.forEach((element) => {
      props.deleteProduct(element.productId);
    });
  };
  const updateProduct = () => {
    rowselected.forEach((element) => {
      props.deleteProduct(element.productId);
    });
  };
  const renderButton = (row) => {
    return (
      <Button onClick={() => console.log(`Button clicked for row ${row.id}`)}>
        Click me
      </Button>
    );
  };

  if (list.length > 0) {
    return (
      <div style={{ height: 400, width: "100%" }}>
        <DataGrid
          rows={list}
          columns={columns}
          pageSize={5}
          textAlign="center"
          rowsPerPageOptions={[5]}
          checkboxSelection
          onSelectionModelChange={(ids) => {
            const selectedIDs = new Set(ids);
            const selectedRowData = list.filter((row) =>
              selectedIDs.has(row.productId)
            );
            setrowselected(selectedRowData);
          }}
          getRowId={(row) => row.productId}
          renderCell={(params) => {
            if (params.column.field === "Name") {
              return renderButton(params.data);
            }
            return params.value;
          }}
        />
        <div
          style={{
            textAlign: "right",
            padding: "20px",
            display: "flex",
            "justify-content": "space-between",
          }}
        >
          <UpdateButton
            deleteProduct={deleteProduct}
            rowSelected={rowselected}
          />
          <DeleteButton
            deleteProduct={deleteProduct}
            rowSelected={rowselected}
          />
        </div>
      </div>
    );
  }
}
